<template>
  <div id="station">
      <el-table
      :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)"
      ref="stationtip"
      @selection-change="select"
      style="width: 100%;margin-top:20px;">
      <el-table-column
      type="selection"
      width="55">
      </el-table-column>
      <el-table-column
        sortable
        prop="create_date"
        label="日期"
        width="300">
      </el-table-column>
      <el-table-column
        prop="message"
        label="内容"
        width="">
      </el-table-column>
      <el-table-column
        prop="status"
        label="标签"
        width="80"
        :filters="[{ text: '未读', value: '未读' }, { text: '已读', value: '已读' }]"
        :filter-method="filterTag"
        filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.status == '未读' ? 'primary' : 'success'"
            close-transition>{{scope.row.status}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
          <el-button @click="mark(scope.row)" type="text" size="small">查看</el-button>
          <el-button type="text" size="small" @click="deleteone(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button type="text" @click="deleteSelected">删除所选</el-button>
    <!-- <el-button type="text" disabled v-if="!quanshan">删除所选</el-button> -->
    <el-button type="text" @click="markSelected">标记为已读</el-button>
    <!-- <el-button type="text" disabled v-if="!quanshan">标记为已读</el-button> -->
    <div class="fenye">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[2,3,5,7]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="this.tableData.length">
      </el-pagination>
    </div>


    <!-- 对话框区域，注意区分 -->
    <el-dialog
      title="通知"
      :visible.sync="toMark"
      width="30%">
      <span>
        {{ content }}
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="toMark = false">取 消</el-button>
        <el-button type="primary" @click="toMark = false">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 删除单条消息时的对话框 -->
    <el-dialog
      title="警告"
      :visible.sync="toDelete"
      width="30%">
      <span>
        您确定要删除该条消息吗？
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="toDelete = false">取 消</el-button>
        <el-button type="primary" @click="ensureToDelete">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除所选消息时的对话框 -->
    <el-dialog
      title="警告"
      :visible.sync="toDeleteSelected"
      width="30%">
      <span>
        您确定要删除该条消息吗？
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="toDelete = false">取 消</el-button>
        <el-button type="primary" @click="ensureToDelete">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { setTimeout } from 'timers';

export default {
  data() {
      return {
        currpage: '1',
        pagesize: 5,
        allID: null,
        toDeleteSelected: false,
        toDelete: false,
        deleteID: null,
        content: null,
        toMark: false,
        selected: [],
        quanshan: 0,
        quanshan01: 0,
        tipNum: 0,
        tableData: []
      }
    },
  methods:{
    handleSizeChange(val) {
      // console.log(`每页 ${val} 条`);
      this.pagesize = val;
    },
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`);
      this.currpage = val;
    },
    filterTag(value, row) {  // 未读和已读的过滤器
      return row.status === value;
    },
    markSelected(){ // 将所选项标记为已读
      // var markLater = [];
      // for (let j = 0; j < this.selected.length; j++) {
      //   markLater.push(this.selected[j].id);
      // }
      // console.log(markLater);
      // this.mark(this.allID);
      var _this = this;
      $.ajax({
        type: 'POST',
        data: {
          mess_id: this.allID.toString()
        },
        url: _this.baseUrl + "/ydjxzg/mb/upmess.action",
        dataType: 'json',
        success: function(res){
          // console.log(res, 'res');
          _this.getTipNum();
        }
      })
    },
    deleteSelected(){ // 将所选项删除
      // var _this = this;
      // var deleteLater = [];
      // for (let i = 0; i < this.selected.length; i++) {
      //   deleteLater.push(this.selected[i].id);
      // }
      // // console.log(deleteLater);
      // this.deleteID = deleteLater;

      // console.log(this.deleteID);
      this.toDeleteSelected = true;
    },
    select(val){ // 组件内的功能，当有消息被选中时触发，与在watch中写的监听机理一致
      this.selected = val;
      // console.log(this.selected, 'selected');
      var allID = [];
      for (let q = 0; q < this.selected.length; q++) {
        allID.push(this.selected[q].mess_id);
      }
      this.allID = allID;
      this.deleteID = allID;
      sessionStorage.setItem('quanxuan01', val.length)
      this.quanshan = sessionStorage.getItem('quanshan01')
      // this.quanshan01 = val.length;
    },
    toggleSelection(rows) { // 组件自带，目前无用
      if (rows) {
        // rows.forEach(row => {
        //   this.$refs.stationtip.toggleRowSelection(row);
        // });
        // console.log(rows, '组件自带')
      } else {
        this.$refs.stationtip.clearSelection();
      }
    },
    ensureToDelete(){ // 确定删除消息
      // console.log(this.deleteID);
      var _this = this;
      $.ajax({
        type: 'POST',
        data: {
          mess_id: this.deleteID.toString()
        },
        url: _this.baseUrl + "/ydjxzg/mb/demess.action",
        dataType: 'json',
        success: function(res){
          _this.$message.success('删除成功！')
          // console.log(res, 'res');
          _this.getTipNum();
        }
      })
      this.toDelete = false;
      this.toDeleteSelected = false;
    },
    deleteone(row){ // 删除单条消息
      this.deleteID = row.mess_id;
      this.toDelete = true;
      // console.log(row.id);
    },
    mark(row) { // 查看某条消息
      this.content = row.message;
      // console.log(row);
      var _this = this;
      $.ajax({
        type: 'POST',
        data: {
          mess_id: row.mess_id.toString()
        },
        url: _this.baseUrl + "/ydjxzg/mb/upmess.action",
        dataType: 'json',
        success: function(res){
          // console.log(res, 'res');
          _this.getTipNum();
        }
      })
      this.toMark = true;
    },
    getTipNum(){  // 请求获取消息列表
      var _this = this;
      var user_id = sessionStorage.getItem("user_id");
      // console.log(user_id, 'user_id');
      $.ajax({
        type: 'POST',
        data: {
          user_id: user_id
        },
        url: _this.baseUrl + "/ydjxzg/mb/getmess.action",
        dataType: 'json',
        success: function(res){
          // console.log(res, 'res')
          var zz = [];
          for (let z = 0; z < res.mess.length; z++) {
            zz.unshift(res.mess[z]);
          }
          _this.tableData = zz;
          // console.log(zz)
        }
      })
      // 给tipNum赋值，使header中的红点能够及时刷新
      var tipnum = [];
      // console.log(this.tableData, 'this.tableData');
      for (let k = 0; k < this.tableData.length; k++) {
        if (this.tableData[k].status == '未读') {
          tipnum.push(this.tableData[k]);
        }
      }
      this.tipNum = tipnum.length;
      // console.log(tipnum.length, 'bhbhbhbbh');
      // console.log(this.tipnum)
      this.$emit('func', this.tipNum)
    }
  },
  created(){
    this.getTipNum();
  },
  mounted(){
    setTimeout(this.getTipNum,3000)
    setInterval(this.getTipNum, 60000);
  }
}
</script>

<style scoped>
  .fenye{
    height: 100px;
    width: 800px;
    /* padding: 0 auto; */
    margin: 20px auto;
    text-align: center;
    display: inline-block;
    /* position: absolute; */
  }
</style>



